.future {
  &__join {
    background: #FFFFFF;
    box-shadow: 0px 5px 30px rgba(198, 207, 217, 0.3);
    border-radius: 16px;
    position: relative;
    top: -120px;
    display: flex;
    &::before {
      content: '';
      background-image: url('../img/future/future-decor.png');
      width: 35px;
      height: 100%;
      background-size: contain;
      display: inline-block;
      position: absolute;
      left: 50%;
      transform: translate(-50%);
      top: 0;
      z-index: 1; }
    span {
      color: #53BF51; }
    &__text {
      padding: 65px;
      width: 100%;
      flex: 1 1 0;
      position: relative;
      z-index: 2;
      background: #fff;
      box-sizing: border-box;
      h2 {
        font-weight: bold;
        font-size: 36px;
        color: #050606;
        margin-bottom: 40px; }
      p {
        font-size: 24px;
        color: #050606;
        font-weight: normal;
        line-height: 1.4;
        span {
          font-weight: bold; } } }
    &__form {
      width: 100%;
      flex: 1 1 0;
      padding: 30px 30px 45px;
      box-sizing: border-box;
      h3 {
        font-weight: bold;
        font-size: 24px;
        margin-bottom: 50px; } } } }

